export default {
  name: 'dragVerify',
  props: {
    // 是否通过
    isPassing: {
      type: Boolean,
      default: false
    },
    // 宽度
    width: {
      type: Number,
      default: 250
    },
    // 高度
    height: {
      type: Number,
      default: 40
    },
    // 组件文案
    text: {
      type: String,
      default: 'swiping to the right side'
    },
    // 成功文案
    successText: {
      type: String,
      default: 'success'
    },
    // 背景色
    background: {
      type: String,
      default: '#eee'
    },
    // 解锁中背景色
    progressBarBg: {
      type: String,
      default: '#76c61d'
    },
    // 解锁成功背景色
    completedBg: {
      type: String,
      default: '#76c61d'
    },
    circle: {
      type: Boolean,
      default: false
    },
    radius: {
      type: String,
      default: '4px'
    },
    handlerIcon: {
      type: String
    },
    successIcon: {
      type: String
    },
    handlerBg: {
      type: String,
      default: '#fff'
    },
    textSize: {
      type: String,
      default: '14px'
    },
    textColor: {
      type: String,
      default: '#333'
    }
  },
  mounted: function mounted() {
    var dragEl = this.$refs.dragVerify;
    dragEl.style.setProperty('--textColor', this.textColor);
    dragEl.style.setProperty('--width', Math.floor(this.width / 2) + 'px');
    dragEl.style.setProperty('--pwidth', -Math.floor(this.width / 2) + 'px');
  },
  computed: {
    handlerStyle: function handlerStyle() {
      return {
        left: '0px',
        width: this.height + 'px',
        height: this.height + 'px',
        background: this.handlerBg
      };
    },
    message: function message() {
      return this.isPassing ? this.successText : this.text;
    },
    dragVerifyStyle: function dragVerifyStyle() {
      return {
        width: this.width + 'px',
        height: this.height + 'px',
        lineHeight: this.height + 'px',
        background: this.background,
        borderRadius: this.circle ? this.height / 2 + 'px' : this.radius
      };
    },
    progressBarStyle: function progressBarStyle() {
      return {
        background: this.progressBarBg,
        height: this.height + 'px',
        borderRadius: this.circle ? this.height / 2 + 'px 0 0 ' + this.height / 2 + 'px' : this.radius
      };
    },
    textStyle: function textStyle() {
      return {
        height: this.height + 'px',
        width: this.width + 'px',
        fontSize: this.textSize
      };
    }
  },
  data: function data() {
    return {
      isMoving: false,
      x: 0,
      isOk: false
    };
  },
  methods: {
    dragStart: function dragStart(e) {
      if (!this.isPassing) {
        this.isMoving = true;
        var handler = this.$refs.handler;
        this.x = (e.pageX || e.touches[0].pageX) - parseInt(handler.style.left.replace('px', ''), 10);
      }

      this.$emit('handlerMove');
    },
    dragMoving: function dragMoving(e) {
      if (this.isMoving && !this.isPassing) {
        var _x = (e.pageX || e.touches[0].pageX) - this.x;

        var handler = this.$refs.handler;

        if (_x > 0 && _x <= this.width - this.height) {
          handler.style.left = _x + 'px';
          this.$refs.progressBar.style.width = _x + this.height / 2 + 'px';
        } else if (_x > this.width - this.height) {
          handler.style.left = this.width - this.height + 'px';
          this.$refs.progressBar.style.width = this.width - this.height / 2 + 'px';
          this.passVerify();
        }
      }
    },
    dragFinish: function dragFinish(e) {
      if (this.isMoving && !this.isPassing) {
        var _x = (e.pageX || e.changedTouches[0].pageX) - this.x;

        if (_x < this.width - this.height) {
          this.isOk = true;
          var that = this;
          setTimeout(function () {
            that.$refs.handler.style.left = '0';
            that.$refs.progressBar.style.width = '0';
            that.isOk = false;
          }, 500);
        } else {
          var handler = this.$refs.handler;
          handler.style.left = this.width - this.height + 'px';
          this.$refs.progressBar.style.width = this.width - this.height / 2 + 'px';
          this.passVerify();
        }

        this.isMoving = false;
      }
    },
    passVerify: function passVerify() {
      this.$emit('update:isPassing', true);
      this.isMoving = false;
      var handler = this.$refs.handler;
      handler.children[0].className = this.successIcon;
      this.$refs.progressBar.style.background = this.completedBg;
      this.$refs.message.style['-webkit-text-fill-color'] = 'unset';
      this.$refs.message.style.animation = 'slidetounlock2 3s infinite';
      this.$refs.message.style.color = '#fff';
      this.$emit('passcallback');
    },
    reset: function reset() {
      var oriData = this.$options.data();

      for (var key in oriData) {
        if (oriData.hasOwnProperty(key)) {
          this.$set(this, key, oriData[key]);
        }
      }

      var handler = this.$refs.handler;
      var message = this.$refs.message;
      handler.style.left = '0';
      this.$refs.progressBar.style.width = '0';
      handler.children[0].className = this.handlerIcon;
      message.style['-webkit-text-fill-color'] = 'transparent';
      message.style.animation = 'slidetounlock 3s infinite';
      message.style.color = this.background;
    }
  }
};